import { fetchConToken } from "../helpers/fetch";

export const crearPost = (
	endpoint,
	uid,
	category,
	job_title,
	description,
	experience_level
) => {
	return async (dispatch) => {
		const resp = await fetchConToken(
			endpoint,
			{ categorie_name: category, job_title, description, experience_level },
			"POST"
		);

		const body = await resp.json();

		if (body.error) {
			console.log(body.error);
		}

		if (body.name === "MongoError") {
			dispatch(setError(body));
			return;
		}

		dispatch(setError(true));

		setTimeout(() => {
			dispatch(setError(null));
		}, 3000);
	};
};

export const borrarPostyRequest = (endpoint) => {
	return async (dispatch) => {
		const resp = await fetchConToken(endpoint, {}, "DELETE");

		const body = await resp.json();

		if (body.error) {
			console.log(body.error);
		}

		console.log(body);
	};
};

export const setError = (payload) => ({
	type: "POST_ERROR",
	payload,
});
